@-webkit-keyframes appear-from-inside {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  75% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes appear-from-inside {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  75% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.file-upload .select-files {
  width: 227px;
}
.file-upload .select-files .file-item {
  display: block;
  position: relative;
  width: 227px;
  height: 30px;
  line-height: 30px;
  padding: 0 30px;
  text-align: left;
  border-radius: 4px;
}
.file-upload .select-files .file-item:hover {
  background-color: #F2FAFF;
  text-decoration: none;
}
.file-upload .select-files .file-item span {
  color: #00A0FF;
}
.file-upload input[type="file"] {
  position: absolute;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  z-index: -1;
}
.file-upload input[type="file"] + label {
  display: inline-block;
  max-width: 80%;
  white-space: nowrap;
  cursor: pointer;
  overflow: hidden;
}
.file-upload input[type="file"] + label:hover,
.file-upload input[type="file"] + label:focus {
  color: #39bfd3;
}
.file-upload input[type="file"]:focus + label,
.file-upload input[type="file"].has-focus + label {
  outline: 1px dotted #000;
  outline: -webkit-focus-ring-color auto 5px;
}
.file-upload .drag-upload {
  position: relative;
  background-color: #c8dadf;
  padding: 100px 20px;
  text-align: center;
}
.file-upload .drag-upload.active {
  outline: 2px dashed #92b0b3;
  outline-offset: -10px;
  -webkit-transition: outline-offset 0.15s ease-in-out, background-color 0.15s linear;
  transition: outline-offset 0.15s ease-in-out, background-color 0.15s linear;
}
.file-upload .drag-upload .state .state-uploading {
  font-style: italic;
}
.file-upload .drag-upload .animate {
  -webkit-animation: appear-from-inside 0.25s ease-in-out;
          animation: appear-from-inside 0.25s ease-in-out;
}
.file-upload .drag-upload.is-dragover {
  outline-offset: -20px;
  outline-color: #c8dadf;
  background-color: #fff;
}
.file-upload .upload-rate .rate-control {
  position: relative;
  display: inline-block;
  width: 90px;
  height: 22px;
  background-color: #f2f2f2;
  border-radius: 4px;
  overflow: hidden;
}
.file-upload .upload-rate .rate {
  display: inline-block;
  width: 0;
  height: 22px;
  background-color: #d9d9d9;
  border-radius: 4px 0 0 4px;
  -webkit-transition-property: width;
  transition-property: width;
}
.file-upload .upload-rate .rate.active {
  width: 90px;
  -webkit-transition-property: width;
  transition-property: width;
}
.file-upload .upload-rate .rate-tip {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  width: 90px;
  height: 22px;
  line-height: 22px;
  color: #666;
  font-size: 12px;
  text-align: center;
  overflow: hidden;
}
.file-upload .upload-rate a {
  color: #00A0FF;
}
.file-upload .upload-rate a:hover {
  text-decoration: none;
}



